<template>
  <div class="chen" :class="{ 'transparent': isHomePage, 'scrolled': isScrolled }">
    <div class="nav-container">
      <div class="logo">åˆ›æ–°ç§‘æŠ€</div>
      <button class="mobile-menu-toggle" @click="toggleMenu" aria-label="èœå•">
        {{ menuIcon }}
      </button>
      <nav :class="{ active: isMenuOpen }">
        <li v-for="(item, index) in menuItems" :key="index">
          <router-link 
            :to="item.path" 
            :class="{ active: $route.name === item.name }"
            @click="closeMenu">
            {{ item.label }}
          </router-link>
        </li>
      </nav>
      <div class="auth-section">
        <template v-if="user">
          <UserProfile />
        </template>
        <router-link v-else to="/login" class="login-link">ç™»å½•</router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed } from 'vue'
import { useRoute } from 'vue-router'
import { user as userState, initAuth } from '../store/auth'
import UserProfile from './UserProfile.vue'

export default {
  name: 'Navigation',
  components: {
    UserProfile
  },
  setup() {
    const isMenuOpen = ref(false)
    const route = useRoute()
    const isScrolled = ref(false)
    
    // åˆå§‹åŒ–è®¤è¯çŠ¶æ€
    initAuth()
    
    // ä½¿ç”¨ computed ç¡®ä¿å“åº”å¼
    const user = computed(() => userState.value)
    
    // æ£€æµ‹æ˜¯å¦åœ¨é¦–é¡µ
    const isHomePage = computed(() => route.name === 'Home')
    
    // ç›‘å¬æ»šåŠ¨ï¼Œå®ç°å¯¼èˆªæ é€æ˜æ•ˆæœ
    const handleScroll = () => {
      if (isHomePage.value) {
        isScrolled.value = window.scrollY > 50
      }
    }
    window.addEventListener('scroll', handleScroll)
    
    const menuItems = [
      { name: 'Home', path: '/', label: 'é¦–é¡µä¿¡æ¯' },
      { name: 'About', path: '/about', label: 'å…¬å¸ç®€ä»‹' },
      { name: 'Jobs', path: '/jobs', label: 'æ‹›è˜ä¿¡æ¯' },
      { name: 'Events', path: '/events', label: 'ç­–åˆ’æ´»åŠ¨' },
      { name: 'Team', path: '/team', label: 'å¸ˆèµ„åŠ›é‡' }
    ]
    
    const menuIcon = computed(() => {
      return isMenuOpen.value ? 'âœ•' : 'â˜°'
    })
    
    const toggleMenu = (event) => {
      event.stopPropagation()
      isMenuOpen.value = !isMenuOpen.value
    }
    
    const closeMenu = () => {
      isMenuOpen.value = false
    }
    
    // ç‚¹å‡»é¡µé¢å…¶ä»–åœ°æ–¹å…³é—­èœå•
    const handleClickOutside = (event) => {
      const navContainer = event.target.closest('.nav-container')
      if (!navContainer && isMenuOpen.value) {
        isMenuOpen.value = false
      }
    }
    
    document.addEventListener('click', handleClickOutside)
    
    return {
      isMenuOpen,
      menuItems,
      menuIcon,
      user,
      isHomePage,
      isScrolled,
      toggleMenu,
      closeMenu
    }
  }
}
</script>

<style scoped>
