  <div class="login-container">
    <div class="login-card">
      <div class="login-header">
        <h1>{{ companyInfo.name }}</h1>
        <p>ç”¨æˆ·ç™»å½•</p>
      </div>
      
      <form @submit.prevent="handleLogin" class="login-form">
        <div class="form-group">
          <label for="username">ç”¨æˆ·å</label>
          <input
            id="username"
            v-model="formData.username"
            type="text"
            placeholder="è¯·è¾“å…¥ç”¨æˆ·å"
            required
            autocomplete="username"
          />
        </div>
        
        <div class="form-group">
          <label for="password">å¯†ç </label>
          <input
            id="password"
            v-model="formData.password"
            type="password"
            placeholder="è¯·è¾“å…¥å¯†ç "
            required
            autocomplete="current-password"
          />
        </div>
        
        <div v-if="errorMessage" class="error-message">
          {{ errorMessage }}
        </div>
        
        <div class="action-buttons">
          <button type="submit" class="login-btn" :disabled="isLoading">
            {{ isLoading ? 'ç™»å½•ä¸­...' : 'ç™»å½•' }}
          </button>
          <router-link to="/register" class="register-btn">æ³¨å†Œ</router-link>
        </div>
      </form>
      
      <div class="login-tips">
        <p class="tips-title">æµ‹è¯•è´¦å·ï¼š</p>
        <ul>
          <li>ç®¡ç†å‘˜ï¼š<strong>admin</strong> / <strong>admin123</strong></li>
          <li>æ™®é€šç”¨æˆ·ï¼š<strong>user</strong> / <strong>user123</strong></li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, reactive } from 'vue'
import { useRouter } from 'vue-router'
import { companyInfo } from '../utils/data'
import { login as authLogin } from '../store/auth'
import '../assets/css/login.css'

export default {
  name: 'Login',
  setup() {
    const router = useRouter()
    const isLoading = ref(false)
    const errorMessage = ref('')
    
    const formData = reactive({
      username: '',
      password: ''
    })
    
    const handleLogin = async () => {
      errorMessage.value = ''
      isLoading.value = true
      
      try {
        const result = await authLogin(formData.username, formData.password)
        
        if (result.success) {
          // ç™»å½•æˆåŠŸï¼Œé‡å®šå‘åˆ°é¦–é¡µæˆ–ä¹‹å‰è®¿é—®çš„é¡µé¢
          const redirect = router.currentRoute.value.query.redirect || '/'
          router.push(redirect)
        } else {
          errorMessage.value = result.message
        }
      } catch (error) {
        console.error('Login error:', error)
        errorMessage.value = 'ç™»å½•å¤±è´¥ï¼Œè¯·ç¨åé‡è¯•'
      } finally {
        isLoading.value = false
      }
    }
    
    return {
      companyInfo,
      formData,
      isLoading,
      errorMessage,
      handleLogin
    }
  }
}
</script>

