<template>
  <div class="register-container">
    <div class="register-card">
      <div class="register-header">
        <h1>{{ companyInfo.name }}</h1>
        <p>ç”¨æˆ·æ³¨å†Œ</p>
      </div>
      
      <form @submit.prevent="handleRegister" class="register-form">
        <div class="form-group">
          <label for="username">ç”¨æˆ·å <span class="required">*</span></label>
          <input
            id="username"
            v-model="formData.username"
            type="text"
            placeholder="è¯·è¾“å…¥ç”¨æˆ·åï¼ˆ3-20ä¸ªå­—ç¬¦ï¼‰"
            required
            minlength="3"
            maxlength="20"
            autocomplete="username"
            :class="{ error: errors.username }"
            @blur="validateUsername"
          />
          <span v-if="errors.username" class="field-error">{{ errors.username }}</span>
        </div>
        
        <div class="form-group">
          <label for="email">é‚®ç®± <span class="required">*</span></label>
          <input
            id="email"
            v-model="formData.email"
            type="email"
            placeholder="è¯·è¾“å…¥é‚®ç®±åœ°å€"
            required
            autocomplete="email"
            :class="{ error: errors.email }"
            @blur="validateEmail"
          />
          <span v-if="errors.email" class="field-error">{{ errors.email }}</span>
        </div>
        
        <div class="form-group">
          <label for="name">å§“å <span class="required">*</span></label>
          <input
            id="name"
            v-model="formData.name"
            type="text"
            placeholder="è¯·è¾“å…¥æ‚¨çš„å§“å"
            required
            maxlength="50"
            :class="{ error: errors.name }"
            @blur="validateName"
          />
          <span v-if="errors.name" class="field-error">{{ errors.name }}</span>
        </div>
        
        <div class="form-group">
          <label for="password">å¯†ç  <span class="required">*</span></label>
          <input
            id="password"
            v-model="formData.password"
            type="password"
            placeholder="è¯·è¾“å…¥å¯†ç ï¼ˆè‡³å°‘6ä¸ªå­—ç¬¦ï¼‰"
            required
            minlength="6"
            autocomplete="new-password"
            :class="{ error: errors.password }"
            @blur="validatePassword"
          />
          <span v-if="errors.password" class="field-error">{{ errors.password }}</span>
        </div>
        
        <div class="form-group">
          <label for="confirmPassword">ç¡®è®¤å¯†ç  <span class="required">*</span></label>
          <input
            id="confirmPassword"
            v-model="formData.confirmPassword"
            type="password"
            placeholder="è¯·å†æ¬¡è¾“å…¥å¯†ç "
            required
            autocomplete="new-password"
            :class="{ error: errors.confirmPassword }"
            @blur="validateConfirmPassword"
          />
          <span v-if="errors.confirmPassword" class="field-error">{{ errors.confirmPassword }}</span>
