<script>
import { ref, reactive } from 'vue'
import { useRouter } from 'vue-router'
import { companyInfo } from '../utils/data'
import { register } from '../api/index'
import { login } from '../store/auth'
import '../assets/css/register.css'

export default {
  name: 'Register',
  setup() {
    const router = useRouter()
    const isLoading = ref(false)
    const errorMessage = ref('')
    const successMessage = ref('')
    
    const formData = reactive({
      username: '',
      email: '',
      name: '',
      password: '',
      confirmPassword: ''
    })
    
    const errors = reactive({
      username: '',
      email: '',
      name: '',
      password: '',
      confirmPassword: ''
    })
    
    // éªŒè¯ç”¨æˆ·å
    const validateUsername = () => {
      if (!formData.username) {
        errors.username = 'è¯·è¾“å…¥ç”¨æˆ·å'
      } else if (formData.username.length < 3) {
        errors.username = 'ç”¨æˆ·åè‡³å°‘éœ€è¦3ä¸ªå­—ç¬¦'
      } else if (formData.username.length > 20) {
        errors.username = 'ç”¨æˆ·åä¸èƒ½è¶…è¿‡20ä¸ªå­—ç¬¦'
      } else if (!/^[a-zA-Z0-9_]+$/.test(formData.username)) {
        errors.username = 'ç”¨æˆ·ååªèƒ½åŒ…å«å­—æ¯ã€æ•°å­—å’Œä¸‹åˆ’çº¿'
      } else {
        errors.username = ''
      }
    }
    
    // éªŒè¯é‚®ç®±
    const validateEmail = () => {
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
      if (!formData.email) {
        errors.email = 'è¯·è¾“å…¥é‚®ç®±'
      } else if (!emailRegex.test(formData.email)) {
        errors.email = 'è¯·è¾“å…¥æœ‰æ•ˆçš„é‚®ç®±åœ°å€'
      } else {
        errors.email = ''
      }
    }
    
    // éªŒè¯å§“å
    const validateName = () => {
      if (!formData.name) {
        errors.name = 'è¯·è¾“å…¥å§“å'
      } else if (formData.name.length > 50) {
        errors.name = 'å§“åä¸èƒ½è¶…è¿‡50ä¸ªå­—ç¬¦'
      } else {
        errors.name = ''
      }
    }
    
    // éªŒè¯å¯†ç 
    const validatePassword = () => {
      if (!formData.password) {
        errors.password = 'è¯·è¾“å…¥å¯†ç '
      } else if (formData.password.length < 6) {
        errors.password = 'å¯†ç è‡³å°‘éœ€è¦6ä¸ªå­—ç¬¦'
      } else {
        errors.password = ''
      }
      // å¦‚æœå·²å¡«å†™ç¡®è®¤å¯†ç ï¼Œé‡æ–°éªŒè¯ç¡®è®¤å¯†ç 
      if (formData.confirmPassword) {
        validateConfirmPassword()
      }
    }
    
    // éªŒè¯ç¡®è®¤å¯†ç 
    const validateConfirmPassword = () => {
      if (!formData.confirmPassword) {
        errors.confirmPassword = 'è¯·å†æ¬¡è¾“å…¥å¯†ç '
      } else if (formData.password !== formData.confirmPassword) {
        errors.confirmPassword = 'ä¸¤æ¬¡è¾“å…¥çš„å¯†ç ä¸ä¸€è‡´'
      } else {
        errors.confirmPassword = ''
      }
    }
    
    // éªŒè¯æ‰€æœ‰å­—æ®µ
    const validateAll = () => {
      validateUsername()
      validateEmail()
      validateName()
      validatePassword()
      validateConfirmPassword()
      
      return !Object.values(errors).some(error => error !== '')
    }
    
    const handleRegister = async () => {
      errorMessage.value = ''
      successMessage.value = ''
      
      // éªŒè¯æ‰€æœ‰å­—æ®µ
      if (!validateAll()) {
        errorMessage.value = 'è¯·ä¿®æ­£è¡¨å•ä¸­çš„é”™è¯¯'
        return
      }
      
      isLoading.value = true
      
      try {
        const response = await register({
          username: formData.username,
          email: formData.email,
          name: formData.name,
          password: formData.password
        })
        
        if (response.success) {
          successMessage.value = response.message
          
          // æ³¨å†ŒæˆåŠŸåè‡ªåŠ¨ç™»å½•
          setTimeout(async () => {
            const loginResult = await login(formData.username, formData.password)
            if (loginResult.success) {
              router.push('/')
            } else {
              router.push('/login')
            }
          }, 1500)
        } else {
          errorMessage.value = response.message
        }
      } catch (error) {
        console.error('Register error:', error)
        errorMessage.value = 'æ³¨å†Œå¤±è´¥ï¼Œè¯·ç¨åé‡è¯•'
      } finally {
        isLoading.value = false
      }
    }
    
    return {
      companyInfo,
      formData,
      errors,
      isLoading,
      errorMessage,
      successMessage,
      validateUsername,
      validateEmail,
      validateName,
      validatePassword,
      validateConfirmPassword,
