<template>
  <div ref="chartRef" class="chart-container" :style="{ width: width, height: height }"></div>
</template>

<script>
import { ref, onMounted, onUnmounted, watch, nextTick, onBeforeUnmount } from 'vue'
import * as echarts from 'echarts'

export default {
  name: 'BaseChart',
  props: {
    options: {
      type: Object,
      required: true
    },
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '400px'
    }
  },
  setup(props) {
    const chartRef = ref(null)
    let chartInstance = null
    
    const initChart = () => {
      if (!chartRef.value) return
      
      const container = chartRef.value
      const width = container.offsetWidth || container.clientWidth
      const height = container.offsetHeight || container.clientHeight || parseInt(props.height) || 400
      
      if (width === 0) {
        // å¦‚æœå®¹å™¨è¿˜æ²¡æœ‰å®½åº¦ï¼Œç­‰å¾…ä¸€ä¸‹å†è¯•
        setTimeout(initChart, 100)
        return
      }
      
      // å¦‚æœå›¾è¡¨å·²ç»åˆå§‹åŒ–ï¼Œå…ˆé”€æ¯
      if (chartInstance) {
        chartInstance.dispose()
        chartInstance = null
      }
      
      // åˆå§‹åŒ–å›¾è¡¨
      chartInstance = echarts.init(chartRef.value, null, {
        width: width,
        height: height
      })
      chartInstance.setOption(props.options)
      
      // ç›‘å¬çª—å£å¤§å°å˜åŒ–
      window.addEventListener('resize', handleResize)
      
      // å»¶è¿Ÿä¸€æ¬¡ resize ç¡®ä¿å›¾è¡¨æ­£ç¡®æ¸²æŸ“
      setTimeout(() => {
        if (chartInstance && chartRef.value) {
          const currentWidth = chartRef.value.offsetWidth || chartRef.value.clientWidth
          const currentHeight = chartRef.value.offsetHeight || chartRef.value.clientHeight || height
          chartInstance.resize({
            width: currentWidth,
            height: currentHeight
          })
        }
      }, 200)
    }
    
    const handleResize = () => {
      if (chartInstance && chartRef.value) {
        chartInstance.resize()
      }
    }
    
    onMounted(() => {
      nextTick(() => {
        initChart()
      })
    })
    
    onBeforeUnmount(() => {
      if (chartInstance) {
        chartInstance.dispose()
        chartInstance = null
      }
      window.removeEventListener('resize', handleResize)
    })
    
    // ç›‘å¬é…ç½®å˜åŒ–
    watch(() => props.options, (newOptions) => {
      if (chartInstance) {
        chartInstance.setOption(newOptions, true)
      } else {
        // å¦‚æœå›¾è¡¨è¿˜æ²¡åˆå§‹åŒ–ï¼Œå°è¯•åˆå§‹åŒ–
        initChart()
      }
    }, { deep: true })
    
    return {
      chartRef
    }
  }
}
</script>

<style scoped>
.chart-container {
  width: 100% !important;
  height: 100% !important;
  min-height: 300px;
  display: block !important;
  box-sizing: border-box !important;
  margin: 0 !important;
  padding: 0 !important;
  flex-shrink: 0 !important;
}
</style>

